import * as React from 'react';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';

const Input = (props) => {
  return (
    <Box
      component="span"
      sx={{
        '& > :not(style)': { m: 1, width: '25ch' },
      }}
      noValidate
      autoComplete="off"
    >
      <TextField name={props.name} value={props.value} onChange={props.onChange} error={props.error} helperText={props.helperText} size="small" id="outlined-basic" label={props.label} variant="outlined" type={props.type} />
    </Box>
  );
}

export default Input;