import * as React from "react";
import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import CustomButton from "./button";
import "../../styles/formDialog.scss";
import DialogTitle from "@mui/material/DialogTitle";

const FormDialog = () => {
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const buttonBunch = (
    <div className="buttonSet">
      <CustomButton>Email</CustomButton>
      <CustomButton>Some</CustomButton>
      <CustomButton>Email</CustomButton>
      <CustomButton>Some</CustomButton>
      <CustomButton>Email</CustomButton>
    </div>
  );

  return (
    <div>
      <Button className="rmBorder" variant="outlined" onClick={handleClickOpen}>
        Saying Hi to my customers
      </Button>
      <Dialog open={open} onClose={handleClose}>
        <DialogTitle>Please say Hi!!!</DialogTitle>
        <DialogContent>
          <h5>
            Fusce quis magna vel ex pellentesque consequat sed et turpis.
            Vivamus bibendum rutrum euismod. Sed non sagittis est, semper
          </h5>
        </DialogContent>
        {buttonBunch}
        <DialogActions>
          <Button variant="contained" color="success" onClick={handleClose}>
            Save
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
};

export default FormDialog;
