import React from "react";
import "../styles/tabNav.scss";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import Box from "@mui/material/Box";
import Collapsible from "./Collapsible";
import ImageWrap from "./imageWrap";
import CustomButton from "./UI/button";

const threeButtons = (
  <div className="buttonSet">
    <CustomButton>Left</CustomButton>
    <CustomButton>Center</CustomButton>
    <CustomButton>Right</CustomButton>
  </div>
);

const TabNav = () => {
  const [value, setValue] = React.useState("one");

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <>
      <Box sx={{ width: "100%" }}>
        <Tabs
          value={value}
          onChange={handleChange}
          aria-label="wrapped label tabs example"
        >
          <Tab value="one" label="Actor"></Tab>
          <Tab value="two" label="Voice" />
          <Tab value="three" label="Alignment" />
          <Tab value="four" label="Background" />
        </Tabs>
      </Box>
      {value === "one" ? <ImageWrap /> : null}
      {value === "two" ? (
        <h5>
          Lorem Ipsum is simply dummy text of the printing and typesetting
          industry. Lorem Ipsum has been the industry's standard dummy text ever
          since the 1500s, when an unknown printer took a galley of type and
          scrambled it to make a type specimen book. It has survived not only
          five centuries, but also the leap into electronic typesetting,
          remaining essentially unchanged. It was popularised in the 1960s with
          the release of Letraset sheets containing Lorem Ipsum passages, and
          more recently with desktop publishing software like Aldus PageMaker
          including versions of Lorem Ipsum
        </h5>
      ) : null}
      {value === "three" ? threeButtons : null}
      {value === "four" ? (
        <>
          <Collapsible header="Images">
            <ImageWrap />
          </Collapsible>
          <Collapsible header="Sample text 1">
            <h5>
              Lorem Ipsum is simply dummy text of the printing and typesetting
              industry. Lorem Ipsum has been the industry's standard dummy text
              ever since the 1500s, when an unknown printer took a galley of
              type and scrambled it to make a type specimen book. It has
              survived not only five centuries, but also the leap into
              electronic typesetting, remaining essentially unchanged. It was
              popularised in the 1960s with the release of Letraset sheets
              containing Lorem Ipsum passages, and more recently with desktop
              publishing software like Aldus PageMaker including versions of
              Lorem Ipsum
            </h5>
          </Collapsible>
          <Collapsible header="Sample text 2">
            <h5>
              Lorem Ipsum is simply dummy text of the printing and typesetting
              industry. Lorem Ipsum has been the industry's standard dummy text
              ever since the 1500s, when an unknown printer took a galley of
              type and scrambled it to make a type specimen book. It has
              survived not only five centuries, but also the leap into
              electronic typesetting, remaining essentially unchanged. It was
              popularised in the 1960s with the release of Letraset sheets
              containing Lorem Ipsum passages, and more recently with desktop
              publishing software like Aldus PageMaker including versions of
              Lorem Ipsum
            </h5>
          </Collapsible>
        </>
      ) : null}
    </>
  );
};

export default TabNav;
