import React, { Component } from "react";
import { ImageContext } from "./Components/Context/imageContext";
import { Route, Switch, Redirect } from "react-router-dom";
import LayoutHome from "./Containers/layoutHome";
import LayoutSignIn from "./Containers/layoutSignIn";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      path: "",
    };
  }

  render() {
    const initialImgContextState = {
      imagePath: this.state.path,
      setImagePath: (img) => {
        this.setState({ path: img });
      },
    };

    return (
      <ImageContext.Provider value={initialImgContextState}>
        <Switch>
          <Route path="/" exact component={LayoutSignIn} />
          <Route path="/home" exact component={LayoutHome} />
          <Redirect to="/" />
        </Switch>
      </ImageContext.Provider>
    );
  }
}

export default App;
