import React, { useContext } from "react";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Typography from "@mui/material/Typography";
import personImage from "../assets/person1.png";
import TextField from "@mui/material/TextField";
import CustomButton from "./UI/button";
import { ImageContext } from './Context/imageContext';
import "../styles/card.scss";

const MediaCard = () => {
  const auth = useContext(ImageContext);
  return (
    <Card sx={{ maxWidth: 800 }}>
      <CardMedia
        component="img"
        height="500"
        image={auth.imagePath === '' ? personImage: auth.imagePath}
        alt="Resource not available."
      />
      <CardContent>
        <Typography gutterBottom variant="h5" component="span">
          <TextField
            className="textField"
            id="outlined-multiline-static"
            multiline
            rows={5}
            placeholder="Type or paste your videoscript here. You can also request a translation of an English script to any of 27 other languages."
          />
        </Typography>
        <CustomButton>Listen</CustomButton>
      </CardContent>
    </Card>
  );
};

export default MediaCard;
