import React, { useState } from "react";
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import FormDialog from "./UI/formDialog";
import BottomNavigation from "@mui/material/BottomNavigation";
import Divider from "@mui/material/Divider";
import "../styles/topNav.scss";
import CustomButton from "./UI/button";
import AlertDialog from "./UI/alertDialog";

const TopNav = () => {

  const [value, setValue] = useState(0);

  const [open, setOpen] = useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <>
    {open ? <AlertDialog btnType="success" contentText="Sorry but there is nothing to save." handleClose={handleClose} handleClickOpen={handleClickOpen} open={open}/> : null}
    <Box sx={{ width: 500 }}>
      <BottomNavigation
        showLabels
        value={value}
        onChange={(event, newValue) => {
          setValue(newValue);
        }}
        component="span"
      >
        {" "}
        <FormDialog>Saying Hi to my customers</FormDialog>
        <CustomButton>Cancel</CustomButton>
        <Button onClick={handleClickOpen} variant="contained" color="success" size="small">
          Save
        </Button>
      </BottomNavigation>
      <Divider variant="middle" />
    </Box>
    </>
  );
};

export default TopNav;
